@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
body {
  margin: 0;
  font-family:'Architects Daughter';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*Header*/

header {
    width: 100%;
    display: flex;
    background-color: #270f0ff2;
    position: fixed;
    z-index: 2;
}

.header-container {
    width: 80%;
    display: flex;
    margin: auto;
}

.header-container img {
    width: 100px;
}

header ul {
    width: 80%;
    display: flex;
    justify-content: space-evenly;
    list-style: none;
    margin: auto;
}

header li {
    padding: 0 0 0 110px;
}

header a {
    text-decoration: none;
    height: 100px;
    color: #cf7f57;
    font-size: 26px;
    cursor: pointer;
}

header a:hover {
    text-decoration: underline;
    color: #cf7f57;
}

.mobile-menu-icons {
    display: none;
}


/*Parallax*/
.intro {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    background: linear-gradient(
            rgba(0, 0, 0, 0.5),
            rgba(0, 0, 0, 0.5)
    );
}

.headline {
    font-size: 130px;
    text-align: center;
    text-transform: capitalize;
    color: #ffffff;
    text-shadow: 2px 3px #181717;
}

.parallax {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 100%;
    height: 100%;
    object-fit: cover;
    background-size: contain;
    z-index: -1;
}

/*About*/

.details {
    background-color: #fffbf9;
    display: flex;
    flex-direction: column;
    padding: 50px 0;
}

.details h3 {
    color: #A95534;
    font-size: 50px;
    text-align: center;
    text-shadow: 1px 2px #181717;
}

.details .wrapper, .details .reverse-wrapper {
    width: 60%;
    display: flex;
    margin: auto;
}

.details-text {
    color: #bc633f;
    margin: auto;
}

.details-text h4 {
    font-size: 32px;
    text-align: center;
    text-shadow: 1px 1px #181717;
}

.details-text p {
    color: #773a23;
    font-size: 24px;
    width: 75%;
    padding: 0 0 0 100px;
}

.reverse-wrapper h4 {
    padding-left: 150px;
}

.reverse-wrapper p {
    padding-left: 200px;
}

.preview {
    width: 330px;
    height: auto;
    border-radius: 50%;
    border: 2px solid #8281815e;
    box-shadow: 1px 5px 22px -4px #000000;
    margin: 0 auto;
}

/*Gallery*/

.alice-carousel__dots, .alice-carousel__prev-btn, .alice-carousel__next-btn {
    display: none;
}

.alice-carousel__wrapper {
    padding-top: 50px;
}

.item  img {
    width: 95%;
    height: auto;
    display: block;
    margin: auto;
    box-shadow: 1px 5px 22px -4px #000000;
}


/*Address*/

.address {
    height: 1000px;
    padding-top: 70px;
}

.ref {
    width: 100%;
    height: 700px;
    position: relative;
}

.address h3 {
    color: #A95534;
    font-size: 50px;
    text-transform: capitalize;
    text-align: center;
    text-shadow: 1px 2px #181717;
}

.address .info-wrapper {
    width: 60%;
    height: 280px;
    left: 0;
    top: -490px;
    position: relative;
    display: flex;
    margin: auto;
    justify-content: space-evenly;
    background: linear-gradient( rgb(0 0 0 / 62%), rgb(0 0 0 / 62%) );
}

.address .info-wrapper h4 {
    color: #ffffff;
    font-size: 32px;
    text-align: center;
    text-shadow: 1px 1px #181717;
}

.address .info-wrapper p {
    color: #ffffff;
    font-size: 24px;
    text-shadow: 1px 1px #181717;
}



/*Menu*/

.tabs-wrapper {
    height: auto;
    padding: 30px 0;
}

.tabs {
    margin: 100px 0 0;
    display: flex;
    justify-content: space-evenly;
    cursor: pointer;
    padding: 0;
}

.tab-item {
    color: #cf7f57;
    font-size: 24px;
    font-weight: bold;
    text-shadow: 1px 1px #181717;
    background-color: #f3cdbbab;
    border: 2px solid #270f0fc2;
    border-radius: 0;
    margin: 0;
    padding: 20px 30px;
    list-style: none;
}

.tab-item:hover {
    background-color: #270f0ff2;
}

.react-tabs__tab--selected {
    background-color: #270f0ff2;
}

.burgers, .lunch, .crepes {
    width: 80%;
    margin: auto;
}

.lunch {
    display: block;
    text-align: center;
}

.burgers ul, .crepes ul {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    padding: 0;
}

.burgers li, .crepes li {
    list-style: none;
    flex: 0 0 33.333333%;
    margin: auto;
}

.burgers img, .crepes img {
    width: 45%;
    height: auto;
    object-fit: cover;
    border: 2px solid #8281815e;
    border-radius: 50%;
    margin: 20px auto;
    box-shadow: 1px 5px 22px -4px #000000;
}

.lunch div {
    display: flex;
    justify-content: space-evenly;
}

.lunch img {
    width: 35%;
    height: auto;
    object-fit: cover;
    border: 2px solid #8281815e;
    margin: 20px auto;
    box-shadow: 1px 5px 22px -4px #000000;
}

.burgers h4, .lunch h4, .crepes h4 {
    color: #A95534;
    font-size: 36px;
    text-shadow: 1px 2px #181717;
}

.burgers span, .lunch span, .crepes span {
    color: #A95534;
    font-size: 30px;
    background-color:#f3cdbbab;
    border-radius: 50%;
    padding: 20px;
    font-weight: bold;
    text-shadow: 1px 2px #181717;
}

.burgers p, .lunch p, .crepes p {
    text-align: center;
    font-size: 20px;
    color: #773a23;
    font-weight: bold;
    padding-top: 30px;
    margin: auto;
} 

.burgers p, .crepes p {
    width: 70%;
}

.burgers button, .lunch button, .crepes button {
    font-family: "Architects Daughter";
    color: #cf7f57;
    font-size: 20px;
    text-shadow: 1px 1px #181717;
    background-color: #f3cdbbab;
    border: 2px solid #270f0fc2;
    border-radius: 0;
    margin-top: 10px;
    padding: 15px 20px;
    list-style: none;
} 

.burgers button:hover, .lunch button:hover, .crepes button:hover {
    background-color: #270f0ff2;
    cursor: pointer;
}

.cart {
    color: #270f0ff2;
    background-color: #f3cdbbab;
    padding: 20px;
    border-radius: 50%;
    font-size: 40px;
    cursor: pointer;
    transform: scale(1);
    -webkit-animation: pulse 2s infinite;
            animation: pulse 2s infinite;
    position: fixed;
    right: 50px;
    bottom: 110px;
}
@-webkit-keyframes pulse {
    0% {
        transform: scale(0.95);
        box-shadow: 0 0 0 0 rgba(243, 205, 187, 0.67);
    } 
    70% {
        transform: scale(1);
        box-shadow: 0 0 0 10px rgba(255, 177, 66, 0);
    } 
    100% {
        transform: scale(0.95);
        box-shadow: 0 0 0 0 rgba(255, 177, 66, 0);
    }
}
@keyframes pulse {
    0% {
        transform: scale(0.95);
        box-shadow: 0 0 0 0 rgba(243, 205, 187, 0.67);
    } 
    70% {
        transform: scale(1);
        box-shadow: 0 0 0 10px rgba(255, 177, 66, 0);
    } 
    100% {
        transform: scale(0.95);
        box-shadow: 0 0 0 0 rgba(255, 177, 66, 0);
    }
}

/*Footer*/

footer {
    background-color: #270f0ff2;
    height: 100px;
    display: flex;
    justify-content: center;
}

footer img {
    width: 100px;
}

footer div {
    display: flex;
}

.socials {
    display: flex;
    width: 50%;
    align-items: center;
    justify-content: center;
}

.socials a {
    padding: 0 20px;
}

.icon {
    color: white;
    font-size: 40px;
}

.icon:hover {
    color: #e29a78;
    font-size: 50px;
    transform: rotate(360deg);
    transition: all 0.4s ease-in-out;
}

footer span {
    color: #ffffff;
    font-size: 20px;
    margin: auto;
    text-shadow: 2px 3px #181717
}

@media only screen and (min-width: 1024px) and (max-width: 1400px){
    .nav-mobile {
        display: none;
    }
    .details .wrapper {
        padding: 20px 0;
    }
    .details .wrapper, .details .reverse-wrapper {
        width: 80%;
    }
    .details-text p {
        width: 85%;
        padding: 0 0 0 50px;
    }
    .address .info-wrapper {
        width: 85%;
    }
    .tabs p {
        width: 90%;
    }
    .burgers li, .crepes li {
        flex: 0 0 33.333333%;
        margin: auto;
    }
    .burgers img, .crepes img {
        width: 90%;
    }
    .burgers span, .crepes span {
        padding: 16px;
    }
    .lunch div {
        display: flex;
        justify-content: space-evenly;
    }
    .lunch img {
        width: 45%;
    }
}

@media only screen and (min-width: 768px) and (max-width: 1023px) {
    .header-container {
        width: 80%;
        display: block;
        margin: auto;
    }
    .mobile-menu-icons {
        display: block;
        position: absolute;
        top: -5px;
        right: 40px;
    }
    header img {
        width: 120px;
    }
    .nav-options {
        display: none;
    }
    .nav-options.active {
        display: block;
        padding: 0;
        transition: all 0.5s ease;
    }
    .option {
        list-style: none;
        color: #cf7f57;
        font-size: 30px;
        padding: 20px;
        text-align: center;
    }
    .open-menu, .close-menu {
        font-size: 30px;
        color: #cf7f57;
        margin: 46px auto;
    }
    .intro {
        height: 700px;
    }
    .parallax {
        width: 100%;
        height: 700px;
    }
    .img-wrapper {
        display: flex;
        margin: auto;
    }
    .headline {
        font-size: 90px;
    }
    .details .wrapper {
        width: 85%;
        display: block;
        text-align: center;
        padding: 20px 0;
    }
    .reverse-wrapper {
        display: flex;
        width: 85%;
        margin: auto;
        text-align: center;
        flex-direction: column-reverse;
        padding: 20px 0;
    }
    .reverse-wrapper h4, .reverse-wrapper p {
        padding: 0;
    }
    .details-text p {
        width: 100%;
        padding: 0;
        text-align: center;
    }
    .address .info-wrapper {
        width: 90%;
    }
    .address .info-wrapper p {
        font-size: 20px;
    }
    .burgers li, .crepes li {
        flex: 0 0 45%;
        margin: auto;
    }
    .burgers img, .crepes img {
        width: 90%;
        margin-top: 30px;
    }
    .burgers span, .crepes span {
        padding: 20px;
    }
    .burgers p, .crepes p {
        width: 100%;
    }
    .lunch div {
        display: grid;
    }
    .lunch img {
        width: 80%;
    }
    .cart {
        bottom: 50px;
    }
}

@media only screen and (min-width: 320px) and (max-width: 767px) {
    .header-container {
        width: 80%;
        display: block;
        margin: auto;
    }
    .mobile-menu-icons {
        display: block;
        position: absolute;
        top: -5px;
        right: 40px;
    }
    .nav-options {
        display: none;
    }
    .nav-options.active {
        display: block;
        padding: 0;
        transition: all 0.5s ease;
    }
    .option {
        list-style: none;
        color: #cf7f57;
        font-size: 30px;
        padding: 20px;
        text-align: center;
    }
    .open-menu, .close-menu {
        font-size: 30px;
        color: #cf7f57;
        margin: 46px auto;
    }
    .intro {
        height: 500px;
    }
    .parallax {
        width: 100%;
        height: 500px;
    }
    .headline {
        font-size: 60px;
    }
    .img-wrapper {
        display: flex;
        margin: auto;
    }
    .preview {
        width: 250px;
        height: 250px;
        object-fit: cover;
    }
    .reverse-wrapper h4, .reverse-wrapper p {
        padding: 0;
    }
    .details .wrapper {
        width: 85%;
        display: block;
        text-align: center;
        padding: 20px 0;
    }
    .reverse-wrapper {
        width: 85%!important;
        margin: auto;
        display: flex;
        text-align: center;
        flex-direction: column-reverse;
        padding: 20px 0;
    }
    .details-text p {
        width: 100%;
        text-align: center;
        padding: 0;
    }
    .address {
        padding: 50px 0 0 0;
    }
    .address h3 {
        font-size: 50px;
    }
    .address .info-wrapper {
        width: 90%;
        height: auto;
        top: -630px;
        display: block;
    }
    .address .info-wrapper p {
        font-size: 22px;
        text-align: center;
    }
    .tabs-wrapper {
        padding-top: 70px;
    }
    .tabs {
        display: block;
        text-align: center;
        padding: 0;
    }
    .tab-item {
        width: 70%;
        padding: 20px 10px;
        margin: 10px auto;
    }
    .burgers li, .crepes li {
        flex: 0 0 100%;
        margin: auto;
    }
    .burgers img, .crepes img {
        width: 70%;
    }
    .burgers p, .crepes p {
        font-size: 24px;
        width: 100%;
    }
    .lunch div {
        display: grid;
    }
    .lunch img {
        width: 80%;
    }
    .cart {
        right: 30px;
        bottom: 30px;
    }
    footer {
        display: block;
    }
    footer .socials {
        margin: 0 auto;
        padding-top: 20px;
    }
}

